import React, { useRef } from 'react';
import { useContainerDimensions } from '../../hooks/useContainerDimensions';
import { roundToDec, getReadableLocalTime } from '../../utils/utils';

import styles from './WeatherCard.module.css';
import windIconPath from '../../images/wind.svg';
import windDirectionPath from '../../images/wind_direction.svg';
import dropIconPath from '../../images/drop.svg';
import gaugeIconPath from '../../images/gauge.svg';
import { useSelector } from 'react-redux';

function WeatherCard() {
  const containerRef = useRef();
  const { width } = useContainerDimensions(containerRef);
  const weather = useSelector(state => state.weather.weather);
  const city = useSelector(state => state.weather.city);

  const weather_weather = weather.weather[0];
  const weather_main = weather.main;

  return (
    <div className={styles.container} ref={containerRef}>
      <div className={styles.heading}>
        <h2 className={styles.title}>{city.name}</h2>
        <p className={styles.subtext}>{getReadableLocalTime(weather.timezone)}</p>
      </div>
      <div className={styles.summary}>
        <p className={styles.currentTemp}>{`${roundToDec(weather_main.temp)}`}</p>
        <img className={styles.icon}
          src={`https://openweathermap.org/img/wn/${weather_weather.icon}@2x.png`}
          alt=' '
        />
        <div className={styles.description}>
          <p className={styles.subtext}>{weather_weather.description}</p>
          <p className={styles.subtext}>{`  ${roundToDec(weather_main.feels_like)}`}</p>
        </div>
      </div>
      <div className={styles.additional} style={{ flexDirection: `${width > 400 ? 'row' : 'column'}` }}>
        <div className={styles.additional__group}>
          <img className={styles.additional__icon} src={windIconPath} alt='' />
          <p className={styles.subtext}>{`${weather.wind.speed} /,`}</p>
          <img
            className={styles.additional__windDirection}
            style={{ transform: `rotate(${weather.wind.deg}deg)` }}
            src={windDirectionPath}
            alt=' '
          />
        </div>
        <div className={styles.additional__group}>
          <img className={styles.additional__icon} src={dropIconPath} alt='' />
          <p className={styles.subtext}>{`${weather_main.humidity}%`}</p>
        </div>
        <div className={styles.additional__group}>
          <img className={styles.additional__icon} src={gaugeIconPath} alt='' />
          <p className={styles.subtext}>{`${weather_main.pressure}  . .`}</p>
        </div>
      </div>
    </div>
  )
}

export default WeatherCard;
